import { useEffect, useState } from "react"
import { ITEMS_PER_PAGE, discountedPrice } from "../../../app/constants"
import { useDispatch, useSelector } from "react-redux"
import {
  fetchAllOrdersAsync,
  selectOrders,
  selectTotalOrders,
  updateOrderAsync,
} from "../../order/orderSlice"
import { ArrowUpIcon, EyeIcon, PencilIcon } from "@heroicons/react/16/solid"
import Pagination from "../../common/Pagination"
// import { selectTotalItems } from "../../product/productSlice"
import { ArrowDownIcon } from "@heroicons/react/24/outline"

function AdminOrders() {
  const [page, setPage] = useState(1)
  const orders = useSelector(selectOrders)
  const totalOrders = useSelector(selectTotalOrders)
  const [editableOrderId, setEditableOrderId] = useState(-1)
  const handleShow = () => {}
  const handleEdit = order => {
    setEditableOrderId(order.id)
  }
  const handleOrderStatus = (e, order) => {
    const updatedOrder = { ...order, status: e.target.value }
    dispatch(updateOrderAsync(updatedOrder))
    setEditableOrderId(-1)
  }
  const handleOrderPaymentStatus = (e, order) => {
    const updatedOrder = { ...order, paymentStatus: e.target.value }
    dispatch(updateOrderAsync(updatedOrder))
    setEditableOrderId(-1)
  }
  const handlePage = page => {
    setPage(page)
    const pagination = { _page: page, _per_page: ITEMS_PER_PAGE }
    dispatch(fetchAllOrdersAsync(pagination))
  }
  const chooseColor = status => {
    switch (status) {
      case "pending":
        return "bg-purple-200 text-purple-600 "
      case "dispatched":
        return "bg-yellow-200 text-yellow-600 "
      case "delivered":
        return "bg-green-200 text-green-600 "
      case "received":
        return "bg-green-200 text-green-600 "
      case "cancelled":
        return "bg-red-200 text-red-600 "
      default:
        return "bg-purple-200 text-purple-600 "
    }
  }
  const dispatch = useDispatch()
  const [sort, setSort] = useState({})
  const handleSort = sortOption => {
    const sort = { _sort: sortOption.sort, _order: sortOption.order }
    // console.log({ sort });
    setSort(sort)
  }
  useEffect(() => {
    const pagination = { _page: page, _per_page: ITEMS_PER_PAGE }
    dispatch(fetchAllOrdersAsync({ sort, pagination }))
  }, [dispatch, page, sort])
  // console.log(orders);
  return (
    <div>
      <div className="overflow-x-auto">
        <div className="  flex items-center justify-center bg-gray-100 font-sans overflow-hidden">
          <div className="w-full">
            <div className="bg-white shadow-md rounded my-6">
              <table className="w-full table-auto">
                <thead>
                  <tr className="bg-gray-200 text-gray-600 uppercase text-sm leading-normal">
                    <th
                      className="py-3 px-6 text-left cursor-pointer"
                      onClick={e =>
                        handleSort({
                          sort: "id",
                          order: sort?._order === "asc" ? "desc" : "asc",
                        })
                      }
                    >
                      Order#
                      {sort._sort === "id" &&
                        (sort._order === "asc" ? (
                          <ArrowUpIcon className="w-4 h-4 inline"></ArrowUpIcon>
                        ) : (
                          <ArrowDownIcon className="w-4 h-4 inline"></ArrowDownIcon>
                        ))}
                    </th>
                    <th className="py-3 px-1 text-left">Items</th>
                    <th
                      onClick={e =>
                        handleSort({
                          sort: "totalAmount",
                          order: sort?._order === "asc" ? "desc" : "asc",
                        })
                      }
                      className="py-3 px-1 text-center"
                    >
                      Total amount{" "}
                      {sort._sort === "totalAmount" &&
                        (sort._order === "asc" ? (
                          <ArrowUpIcon className="w-4 h-4 inline"></ArrowUpIcon>
                        ) : (
                          <ArrowDownIcon className="w-4 h-4 inline"></ArrowDownIcon>
                        ))}
                    </th>
                    <th className="py-3 px-1 text-center">Shipping Address</th>
                    <th className="py-3 px-1 text-center">Order Status</th>
                    <th className="py-3 px-1 text-center">Payment Method</th>
                    <th className="py-3 px-1 text-center">Payment Status</th>
                    <th
                      onClick={e =>
                        handleSort({
                          sort: "createdAt",
                          order: sort?._order === "asc" ? "desc" : "asc",
                        })
                      }
                      className="py-3 px-1 text-center"
                    >
                      Order Time
                      {sort._sort === "createdAt" &&
                        (sort._order === "asc" ? (
                          <ArrowUpIcon className="w-4 h-4 inline"></ArrowUpIcon>
                        ) : (
                          <ArrowDownIcon className="w-4 h-4 inline"></ArrowDownIcon>
                        ))}
                    </th>
                    <th
                      onClick={e =>
                        handleSort({
                          sort: "updatedAt",
                          order: sort?._order === "asc" ? "desc" : "asc",
                        })
                      }
                      className="py-3 px-1 text-center"
                    >
                      Last Updated
                      {sort._sort === "updatedAt" &&
                        (sort._order === "asc" ? (
                          <ArrowUpIcon className="w-4 h-4 inline"></ArrowUpIcon>
                        ) : (
                          <ArrowDownIcon className="w-4 h-4 inline"></ArrowDownIcon>
                        ))}
                    </th>
                    <th className="py-3 px-1 text-center">Actions</th>
                  </tr>
                </thead>
                <tbody className="text-gray-600 text-sm font-light">
                  {orders &&
                    orders.map(order => (
                      <tr className="border-b border-gray-200 hover:bg-gray-100">
                        <td className="py-3 px-1 text-left whitespace-nowrap">
                          <div className="flex items-center">
                            <div className="mr-2"></div>
                            <span className="font-medium">{order.id}</span>
                          </div>
                        </td>
                        <td className="py-3 px-1 text-left">
                          {order.items.map(item => (
                            <div className="flex items-center">
                              <div className="mr-2">
                                <img
                                  className="w-15 h-12"
                                  src={item.product.thumbnail}
                                  alt={item.product.title}
                                />
                              </div>
                              <span>
                                {item.product.title} - #{item.quantity} - $
                                {item.product.discountPrice}
                              </span>
                            </div>
                          ))}
                        </td>
                        <td className="py-3 px-1 text-center">
                          <div className="flex items-center justify-center">
                            ${order.totalAmount}
                          </div>
                        </td>
                        <td className="py-3 px-1 text-center">
                          <div className="">
                            <strong>{order.selectedAddress.name}</strong>
                            <div>
                              {order.selectedAddress.phone}
                              {order.selectedAddress.street},
                            </div>
                            <div>
                              {order.selectedAddress.city},
                              {order.selectedAddress.state},
                            </div>
                            <div>
                              {order.selectedAddress.country},
                              {order.selectedAddress.pinCode}
                            </div>
                          </div>
                        </td>
                        <td className="py-3 px-1 text-center">
                          {order.id !== editableOrderId ? (
                            <span
                              className={`${chooseColor(order.status)}py-1 px-3 rounded-full text-xs`}
                            >
                              {order.status}
                            </span>
                          ) : (
                            <select onChange={e => handleOrderStatus(e, order)}>
                              <option value="pending">Pending</option>
                              <option value="dispatched">Dispatched</option>
                              <option value="delivered">Delivered</option>
                              <option value="cancelled">Cancelled</option>
                            </select>
                          )}
                        </td>
                        <td className="py-3 px-1 text-center">
                          <div className="flex items-center justify-center">
                            {order.paymentMethod}
                          </div>
                        </td>

                        <td className="py-3 px-1 text-center">
                          {order.id !== editableOrderId ? (
                            <span
                              className={`${chooseColor(order.paymentStatus)}py-1 px-3 rounded-full text-xs`}
                            >
                              {order.paymentStatus}
                            </span>
                          ) : (
                            <select
                              onChange={e => handleOrderPaymentStatus(e, order)}
                            >
                              <option value="pending">Pending</option>
                              <option value="received">Received</option>
                            </select>
                          )}
                        </td>
                        <td className="py-3 px-1 text-center">
                          <div className="flex items-center justify-center">
                            {order.createdAt
                              ? new Date(order.createdAt).toLocaleString()
                              : ""}
                          </div>
                        </td>
                        <td className="py-3 px-1 text-center">
                          <div className="flex items-center justify-center">
                            {order.updatedAt
                              ? new Date(order.updatedAt).toLocaleString()
                              : ""}
                          </div>
                        </td>

                        <td className="py-3 px-1 text-center">
                          <div className="flex item-center justify-center">
                            <div className="w-6 mr-4 transform hover:text-purple-500 hover:scale-110">
                              <EyeIcon
                                onClick={e => handleShow(order)}
                              ></EyeIcon>
                            </div>
                            <div className="w-6 mr-3 transform hover:text-purple-500 hover:scale-110">
                              <PencilIcon
                                onClick={e => handleEdit(order)}
                              ></PencilIcon>
                            </div>
                          </div>
                        </td>
                      </tr>
                    ))}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
      <Pagination
        totalItems={totalOrders}
        page={page}
        setPage={setPage}
        handlePage={handlePage}
      ></Pagination>
    </div>
  )
}

export default AdminOrders
