export function fetchLoggedInUserOrders() {
  const fetchData = async () => {
    // console.log(userId);
    const response = await fetch("/orders/own/")
    if (response.ok) {
      const data = await response.json()
      // console.log({ data })
      // console.log(data);
      return { data }
    }
    else{
      const error=await response.json();
      // console.log(error);
      throw new Error(error.message)
    }
  }
  return fetchData()
}

