import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { createAppSlice } from "../../app/createAppSlice"
import { createOrder, fetchAllOrders, fetchCount, updateOrder } from "./orderAPI"

const initialState = {
  orders:[],
  status: "idle",
  currentOrder:null,
  totalOrders:0
}


export const createOrderAsync = createAsyncThunk(
  'orders/createOrder',
  async ( userId ) => {
    try {
      // console.log(userId+" inside slice");
      const response = await createOrder(userId);
      return response;
    } catch (error) {
      console.error("Error fetching products:", error);
      return Promise.reject(error);
    }
  }
);
export const fetchAllOrdersAsync = createAsyncThunk(
  'orders/fetchAllOrders',
  async ( {sort, pagination} ) => {
    try {
      const response = await fetchAllOrders({sort,pagination});
      // console.log(response);
      return response;
    } catch (error) {
      console.error("Error fetching products:", error);
      return Promise.reject(error);
    }
  }
);
export const updateOrderAsync = createAsyncThunk(
  'orders/updateOrder',
  async ( order ) => {
    try {
      // console.log(userId+" inside slice");
      const response = await updateOrder(order);
      return response;
    } catch (error) {
      console.error("Error fetching products:", error);
      return Promise.reject(error);
    }
  }
);
export const orderSlice = createSlice({
  name: 'orders',
  initialState,
  reducers: {
    resetOrder:(state)=>{
      state.currentOrder=null;
    }
  },
  extraReducers: (builder) => {
    builder
      .addCase(createOrderAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(createOrderAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.orders.push(action.payload);
        state.currentOrder=action.payload;
      })
      .addCase(fetchAllOrdersAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchAllOrdersAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.orders=action.payload.data.products;
        state.totalOrders=action.payload.data.totalItems;
        // console.log(action.payload.data);
      })
      .addCase(updateOrderAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(updateOrderAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        const index=state.orders.findIndex(order=>order.id===action.payload.id)
        state.orders[index]=action.payload;
      })
  },
});
export const {resetOrder}=orderSlice.actions;
export const selectCurrentOrder=(state)=>state.orders.currentOrder;
export const selectTotalOrders=(state)=>state.orders.totalOrders;
export const selectOrders=(state)=>state.orders.orders;
export const selectStatus=(state)=>state.orders.status;
export default orderSlice.reducer;


